import React from 'react';
import './main.css';
import man from '../../images/logo/LOGO_SofLednik.png';

const Main = () => {
    return (
        <main className="main">
            <h1>
                Доставка кристально чистой природной воды из источников гор
                Северного Кавказа
            </h1>
            <img className="manWithBottle" src={man} alt="man with bottle" />
        </main>
    );
};

export default Main;
