import React from 'react';
import './app.css';
import WrapperHeaderImg from '../wrapperHeaderImg/WrapperHeaderImg';
import Main from '../main/Main';
import Sidebar from '../sidebar/Sidebar';
import Price from '../price/Price';
import { useState } from 'react';
import SidebarChooseCity from '../sidebarChooseCity/SidebarChooseCity';
import About from '../about/About';
import More from './../more/More';
import '../mediaQueryes.css';

const App = () => {
    const [city, setCity] = useState('Сочи');

    const showSidebarCity = (e) => {
        e.preventDefault();
        if (
            document.documentElement.clientWidth < 767 &&
            document.documentElement.clientWidth > 800
        ) {
            document
                .querySelector('.sidebarMap')
                .classList.add('showMapTablet');
        } else {
            document.querySelector('.sidebarMap').classList.add('showMap');
        }
    };

    const hideSidebarCity = () => {
        document
            .querySelector('.sidebarMap')
            .classList.remove('showMap', 'showMapTablet');
    };

    const openModalHamburger = () => {
        const sidebar = document.querySelector('.sidebar');
        if (sidebar.style.display === 'none') {
            sidebar.style.display = 'block';
            for (let i = 1; i < 4; i++) {
                document
                    .querySelector(`.header__ham${i}`)
                    .classList.add(`header__rotate${i}`);
            }
        } else {
            sidebar.style.display = 'none';
            for (let i = 1; i < 4; i++) {
                document
                    .querySelector(`.header__ham${i}`)
                    .classList.remove(`header__rotate${i}`);
            }
        }
    };

    return (
        <>
            {/* <Header
                showSidebarCity={showSidebarCity}
                hideSidebarCity={hideSidebarCity}
                city={city}
                setCity={setCity}
            /> */}
            <div class="header__hamburger" onClick={openModalHamburger}>
                <span class="header__ham1"></span>
                <span class="header__ham2"></span>
                <span class="header__ham3"></span>
            </div>
            <Sidebar
                city={city}
                setCity={setCity}
                showSidebarCity={showSidebarCity}
                hideSidebarCity={hideSidebarCity}
            />
            <SidebarChooseCity
                setCity={setCity}
                hideSidebarCity={hideSidebarCity}
            />
            <WrapperHeaderImg>
                <Main />
                <About />
                <Price
                    city={city}
                    showSidebarCity={showSidebarCity}
                    hideSidebarCity={hideSidebarCity}
                />
                {/* <Reviews /> */}
                {/* <Chemistry /> */}
                {/* <Certificate /> */}
                <More showSidebarCity={showSidebarCity} city={city} />
                {/* <Footer /> */}
            </WrapperHeaderImg>
        </>
    );
};

export default App;
