import React from 'react';
import './sidebarChooseCity.css';
import cityImage from '../../images/free-icon-building-188379.png';

const SidebarChooseCity = ({ setCity, hideSidebarCity }) => {
    const setNewCity = (e) => {
        e.preventDefault();

        setCity(e.target.innerHTML);
    };
    return (
        <div className="sidebarMap">
            <span onClick={hideSidebarCity} class="material-icons closeSidebar">
                close
            </span>
            <div className="city__img__flex">
                <img className="city__img" src={cityImage} />
            </div>
            <h2 className="sidebarMap__header">Выберите свой город</h2>
            <ul className="city__ul">
                <li>
                    <a href="#" onClick={(e) => setNewCity(e)}>
                        Сочи
                    </a>
                </li>
                <li>
                    <a href="#" onClick={(e) => setNewCity(e)}>
                        Краснодар
                    </a>
                </li>
            </ul>
        </div>
    );
};

export default SidebarChooseCity;
