import React, { useState } from 'react';
import './price.css';
import emailjs from 'emailjs-com';
import { useEffect } from 'react';

import bottle1 from '../../images/itemsForSell/photo_2021-06-06_23-54-08.jpg';
import bottle2 from '../../images/itemsForSell/photo_2021-06-06_23-54-26.jpg';
import bottle3 from '../../images/itemsForSell/photo_2021-06-06_23-54-44.jpg';
import bottle4 from '../../images/itemsForSell/photo_2021-06-06_23-54-49.jpg';
import bottle5 from '../../images/itemsForSell/photo_2021-06-06_23-54-32.jpg';
import bottle6 from '../../images/itemsForSell/IMG_6440.JPG';
import bottle7 from '../../images/itemsForSell/img_6441.jpg';

const emailData = {
    SERVICE_ID: `gmail`, //serviceId
    TEMPLATE_ID: `template_8lmpp63`, //templateID
    USER_ID: 'user_UEJEBZBNK3zA1qtmse7IR',
};

let moreThanTwo = true;
let moreThanTen = true;

const Price = ({ city, showSidebarCity, hideSidebarCity }) => {
    const [order, changeOrder] = useState([]);

    const [price, change] = useState([
        { price: 101, name: 'Вода 0,5 л', img: bottle1, counter: 0 },
        { price: 102, name: 'Вода 1 л', img: bottle2, counter: 0 },
        { price: 270, name: 'Вода 18,9 л', img: bottle3, counter: 0 },
        { price: 250, name: 'Вода 18,9 л', img: bottle4, counter: 0 },
        { price: 105, name: 'Вода 0,5 л', img: bottle5, counter: 0 },
        { price: 255, name: 'Вода 18,9 л', img: bottle6, counter: 0 },
        { price: 265, name: 'Вода 18,9 л', img: bottle7, counter: 0 },
    ]);

    useEffect(() => {
        changePrice();
    }, [city]);

    const changePrice = () => {
        changeOrder([]);
        if (price[0].price === 101) {
            change((oldValue) => {
                const newArr = [];
                newArr[0] = {
                    price: 201,
                    name: 'Вода 0,5 л',
                    img: bottle1,
                    counter: 0,
                };
                newArr[1] = {
                    price: 202,
                    name: 'Вода 1 л',
                    img: bottle2,
                    counter: 0,
                };
                newArr[2] = {
                    price: 270,
                    name: 'Вода 18,9 л',
                    img: bottle3,
                    counter: 0,
                };
                newArr[3] = {
                    price: 250,
                    name: 'Вода 18,9 л',
                    img: bottle4,
                    counter: 0,
                };
                newArr[4] = {
                    price: 205,
                    name: 'Вода 0,5 л',
                    img: bottle5,
                    counter: 0,
                };
                newArr[5] = {
                    price: 255,
                    name: 'Вода 18,9 л',
                    img: bottle6,
                    counter: 0,
                };
                newArr[6] = {
                    price: 265,
                    name: 'Вода 18,9 л',
                    img: bottle7,
                    counter: 0,
                };
                return newArr;
            });
        } else {
            change((oldValue) => {
                const newArr = [];
                newArr[0] = {
                    price: 101,
                    name: 'Вода 0,5 л',
                    img: bottle1,
                    counter: 0,
                };
                newArr[1] = {
                    price: 102,
                    name: 'Вода 0,5 л',
                    img: bottle2,
                    counter: 0,
                };
                newArr[2] = {
                    price: 170,
                    name: 'Вода 19 л',
                    img: bottle3,
                    counter: 0,
                };
                newArr[3] = {
                    price: 150,
                    name: 'Вода 19 л',
                    img: bottle4,
                    counter: 0,
                };
                newArr[4] = {
                    price: 105,
                    name: 'Вода 0,5 л',
                    img: bottle5,
                    counter: 0,
                };
                newArr[5] = {
                    price: 155,
                    name: 'Вода 18,9 л',
                    img: bottle6,
                    counter: 0,
                };
                newArr[6] = {
                    price: 165,
                    name: 'Вода 18,9 л',
                    img: bottle7,
                    counter: 0,
                };
                return newArr;
            });
        }
    };

    const textUnderButtonSuccess = (id) => {
        document.querySelector(`.itemAdded${id}`).innerHTML =
            'Товар добавлен в корзину ниже';
        document.querySelector(`.itemAdded${id}`).parentNode.style.height =
            '435px';
        setTimeout(() => {
            document.querySelector(`.itemAdded${id}`).parentNode.style.height =
                '410px';
            document.querySelector(`.itemAdded${id}`).innerHTML = '';
        }, 1500);
    };

    const buy = (id) => {
        changeOrder((order) => {
            const copyOrder = [...order];
            if (order.length === 0) {
                copyOrder.push(eval(`price[${id}]`));
                copyOrder[0].counter = 1;
                textUnderButtonSuccess(id);
            } else {
                if (
                    copyOrder.find((water) => water.price === price[id].price)
                ) {
                    price[id].counter += 0.5;
                    textUnderButtonSuccess(id);
                } else {
                    copyOrder.push(eval(`price[${id}]`));
                    price[id].counter = 1;
                    textUnderButtonSuccess(id);
                }
            }
            return copyOrder;
        });
    };

    const addCounter = async (idx) => {
        await changeOrder((order) => {
            const copyOrder = [...order];
            copyOrder[idx].counter += 0.5;
            return copyOrder;
        });

        if (order[idx].counter >= 10) {
            if (order[idx].counter === 10) {
                moreThanTen = true;
            }
            changeOrder((order) => {
                const copyOrder = [...order];
                if (moreThanTen) {
                    copyOrder[idx].price -= 5;
                    moreThanTen = false;
                }
                return copyOrder;
            });
        } else if (order[idx].counter > 1) {
            if (order[idx].counter === 2) {
                moreThanTwo = true;
            }
            changeOrder((order) => {
                const copyOrder = [...order];
                if (moreThanTwo) {
                    copyOrder[idx].price -= 5;
                    moreThanTwo = false;
                }
                return copyOrder;
            });
        }
    };

    const removeCounter = async (idx) => {
        await changeOrder((order) => {
            const copyOrder = [...order];
            copyOrder[idx].counter -= 0.5;
            if (copyOrder[idx].counter === 0) {
                copyOrder.splice(idx, 1);
            }
            return copyOrder;
        });

        if (order[idx].counter === 1) {
            if (order[idx].counter === 1) {
                moreThanTwo = true;
            }
            changeOrder((order) => {
                const copyOrder = [...order];
                if (moreThanTwo) {
                    copyOrder[idx].price += 5;
                    moreThanTwo = false;
                }
                return copyOrder;
            });
        } else if (order[idx].counter < 10) {
            moreThanTen = false;
            if (order[idx].counter === 9) {
                moreThanTen = true;
            }
            changeOrder((order) => {
                const copyOrder = [...order];
                if (moreThanTen) {
                    copyOrder[idx].price += 5;
                    moreThanTen = false;
                }
                return copyOrder;
            });
        }
    };

    const removeItem = (idx) => {
        changeOrder((order) => {
            const copyOrder = [...order];
            if (copyOrder[idx].counter >= 10) {
                copyOrder[idx].price += 5;
            } else if (copyOrder[idx].counter >= 2) {
                copyOrder[idx].price += 2.5;
            }
            copyOrder.splice(idx, 1);
            return copyOrder;
        });
    };

    const showModalOrder = () => {
        document.querySelector('.modal').style.display = 'flex';
    };

    const hideModalOrder = () => {
        document.querySelector('.modal').style.display = 'none';
    };

    const submitAllData = (e) => {
        document.querySelector('#order').value += order.map((item) => {
            return ` ${item.name} ${item.counter}`;
        });
        e.preventDefault();

        emailjs
            .sendForm(
                emailData.SERVICE_ID,
                emailData.TEMPLATE_ID,
                e.target,
                emailData.USER_ID
            )
            .then(
                (result) => {
                    console.log(result.text);
                },
                (error) => {
                    console.log(error.text);
                }
            );

        e.target.reset();
    };

    // console.log(order);

    return (
        <div className="price">
            <h2 id="waterInBottleHeader" className="waterInBottleHeader">
                Вода в бутылях
            </h2>
            <div className="price__city">
                <h3>Ваш город:</h3>
                <div className="city__price">
                    <a
                        href="#"
                        className="city__price__link"
                        onClick={(e) => showSidebarCity(e)}
                    >
                        {city}
                    </a>
                </div>
            </div>
            <div className="price__wrapper">
                <div className="price__cart__wrapper">
                    <div className="price__cart">
                        <img src={price[5].img} alt="photo of water" />
                        <h4>Вода 19,5 л</h4>
                        <h3 className="price__price">{`${price[5].price} руб`}</h3>
                        <button onClick={() => buy(5)} className="price__order">
                            Заказать
                        </button>
                        <p className="itemAdded5"></p>
                    </div>
                </div>

                <div className="price__cart__wrapper">
                    <div className="price__cart">
                        <img src={price[6].img} alt="photo of water" />
                        <h4>Вода 19,5 л</h4>
                        <h3 className="price__price">{`${price[6].price} руб`}</h3>
                        <button onClick={() => buy(6)} className="price__order">
                            Заказать
                        </button>
                        <p className="itemAdded6"></p>
                    </div>
                </div>

                <div className="price__cart__wrapper">
                    <div className="price__cart">
                        <img src={price[2].img} alt="photo of water" />
                        <h4>Вода 19,5 л</h4>
                        <h3 className="price__price">{`${price[2].price} руб`}</h3>
                        <button onClick={() => buy(2)} className="price__order">
                            Заказать
                        </button>
                        <p className="itemAdded2"></p>
                    </div>
                </div>

                <div className="price__cart__wrapper">
                    <div className="price__cart">
                        <img src={price[3].img} alt="photo of water" />
                        <h4>Вода 19 л</h4>
                        <h3 className="price__price">{`${price[3].price} руб`}</h3>
                        <button onClick={() => buy(3)} className="price__order">
                            Заказать
                        </button>
                        <p className="itemAdded3"></p>
                    </div>
                </div>
            </div>

            <h2 id="waterInBottleHeader2" className="waterInBottleHeader">
                Вода в бутылках
            </h2>
            <div className="price__city">
                <h3>Ваш город:</h3>
                <div className="city__price">
                    <a
                        href="#"
                        className="city__price__link"
                        onClick={(e) => showSidebarCity(e)}
                    >
                        {city}
                    </a>
                </div>
            </div>

            <div className="price__wrapper">
                <div className="price__cart__wrapper">
                    <div className="price__cart">
                        <img src={price[0].img} alt="photo of water" />
                        <h4>Вода 19,5 л</h4>
                        <h3 className="price__price">{`${price[0].price} руб`}</h3>
                        <button onClick={() => buy(0)} className="price__order">
                            Заказать
                        </button>
                        <p className="itemAdded0"></p>
                    </div>
                </div>

                <div className="price__cart__wrapper">
                    <div className="price__cart">
                        <img src={price[1].img} alt="photo of water" />
                        <h4>Вода 19,5 л</h4>
                        <h3 className="price__price">{`${price[1].price} руб`}</h3>
                        <button onClick={() => buy(1)} className="price__order">
                            Заказать
                        </button>
                        <p className="itemAdded1"></p>
                    </div>
                </div>

                <div className="price__cart__wrapper">
                    <div className="price__cart">
                        <img src={price[4].img} alt="photo of water" />
                        <h4>Вода 19,5 л</h4>
                        <h3 className="price__price">{`${price[4].price} руб`}</h3>
                        <button onClick={() => buy(4)} className="price__order">
                            Заказать
                        </button>
                        <p className="itemAdded4"></p>
                    </div>
                </div>
            </div>

            <h2 id="price__header" className="price__header">
                Доставка и оплата
            </h2>
            {order.length ? (
                <div>
                    <div className="price__wrapper">
                        {order.map((item, idx) => {
                            {
                                /* if (
                                document.querySelector('.adToMoreBuy') &&
                                item.counter >= 10
                            ) {
                                document.querySelectorAll('.adToMoreBuy')[
                                    idx
                                ].style.display = 'none';
                            } else if (
                                document.querySelector('.adToMoreBuy') &&
                                item.counter < 10
                            ) {
                                document.querySelectorAll('.adToMoreBuy')[
                                    idx
                                ].style.display = 'block';
                            } */
                            }
                            return (
                                <div className="price__cart__wrapper">
                                    <div className="price__cart__buy">
                                        <img
                                            src={item.img}
                                            alt="photo of water"
                                        />
                                        <h4>Вода 19,5 л</h4>
                                        <h3 className="price__price">{`${item.price} руб`}</h3>
                                        <p className="price__counter">
                                            Количество: {item.counter}
                                        </p>
                                        <div className="addAndRemove">
                                            <span
                                                onClick={() => addCounter(idx)}
                                                className="material-icons counterChanger"
                                            >
                                                add
                                            </span>
                                            <span
                                                onClick={() =>
                                                    removeCounter(idx)
                                                }
                                                className="material-icons counterChanger"
                                            >
                                                remove
                                            </span>
                                        </div>
                                        <button
                                            onClick={() => removeItem(idx)}
                                            className="removeItem"
                                        >
                                            Удалить
                                        </button>
                                        <p className="adToMoreBuy">
                                            {item.counter < 2
                                                ? `При заказе ещё ${
                                                      item.counter
                                                  } бутыля цена будет ${
                                                      item.price - 5
                                                  }`
                                                : `При заказе ещё ${
                                                      10 - item.counter
                                                  } бутыля цена будет ${
                                                      item.price - 5
                                                  }`}
                                        </p>
                                    </div>
                                </div>
                            );
                        })}
                    </div>
                    <h2 className="allPrice">
                        Общая цена:{' '}
                        {order.reduce((sum, product) => {
                            return sum + product.price * product.counter;
                        }, 0)}{' '}
                        руб
                    </h2>
                    <div className="button__wrapper">
                        <button
                            onClick={() => showModalOrder()}
                            className="toOrder"
                        >
                            Заказать
                        </button>
                    </div>
                    <div className="modal">
                        <div className="modal__window">
                            <span
                                onClick={() => hideModalOrder()}
                                className="material-icons close"
                            >
                                close
                            </span>
                            <h2>Введите ваши данные</h2>
                            <form id="formOrder" onSubmit={submitAllData}>
                                <div className="input__wrapper">
                                    <label
                                        className="label__input"
                                        htmlFor="name"
                                    >
                                        Ваше имя
                                    </label>
                                    <input name="name" type="text" id="name" />
                                </div>
                                <div className="input__wrapper">
                                    <label
                                        className="label__input"
                                        htmlFor="address"
                                    >
                                        Адрес
                                    </label>
                                    <input
                                        name="address"
                                        type="address"
                                        id="address"
                                    />
                                </div>
                                <div className="input__wrapper">
                                    <label
                                        className="label__input"
                                        htmlFor="tel"
                                    >
                                        Номер телефона
                                    </label>
                                    <input
                                        name="phone"
                                        id="tel"
                                        type="number"
                                    />
                                </div>
                                <input
                                    id="order"
                                    name="order"
                                    value=""
                                    type="hidden"
                                />
                                <button type="submit" className="confirm">
                                    Подтвердить
                                </button>
                            </form>
                        </div>
                    </div>
                </div>
            ) : (
                <p className="price__clear">
                    Корзина пуста😞, что бы это исправить выберите товар из
                    списка выше😁
                </p>
            )}
        </div>
    );
};

export default Price;
