import React from 'react';
import './about.css';
import water__descr from '../../images/IMG_7740.JPEG';
import peace__bottom from '../../images/IMG_7764.JPEG';

const About = () => {
    return (
        <div className="about">
            <p className="about__text">
                <h2 className="about__text__header">О воде</h2>
                <br />
                <br /> Первозданная чистота горных вод Северного Кавказа –
                бесценный подарок природы человеку. Проходя через естественный
                фильтр – вулканические породы –вода приобретает кристальную
                чистоту, сохраняя при этом уникальную мягкость.
                <br />
                <br /> Минеральная вода «Софийский ледник» добывается с
                глубинной скважины (скважина № 1 «Софийский ледник») и
                разливается в ПЭТ- и ПК-бутылки на современном оборудовании
                объемом от 0,5 л до 19 л. «Софийский ледник» относится к
                минеральным природным столовым водам и соответствует ТУ
                9185-001-73658411-07 (относится к классу «Премиум»).
                <br />
                <br /> Благодаря своему химическому составу, отсутствию
                противопоказаний и низкой минерализации «Софийский ледник »
                снимает усталость, повышает работоспособность, великолепно
                утоляет жажду. Вода может быть использована для ежедневного
                употребления. «Софийский ледник» является неоднократным
                участником выставок и победителем дегустационных конкурсов, что
                подтверждено многочисленными дипломами и медалями. Минеральную
                воду «Софийский ледник» отличают замечательный вкус и высокое
                качество. Рекомендована Пятигорским НИИ курортологии
                Министерства здравоохранения России.
                <br />
                <br /> Продукт сертифицирован, имеет зарегистрированный товарный
                знак «Софийский ледник» и штрих-коды международной системы
                EAN-UCC. Является участником программы «Качество» «Софийский
                ледник» и Вы полны жизненных сил!
                <br />
                <br /> Производитель: ООО «Софийский ледник», КЧР. Урупский
                р-он, п. Медногорский.
            </p>
            <img className="water__img__bottle" src={peace__bottom} />
        </div>
    );
};

export default About;
